import { CardContent,Typography, Card, CardActions, Button, makeStyles } from '@material-ui/core';
import React from 'react';


const useStyles = makeStyles({
    cover: {
      backgroundImage: `url(https://images.unsplash.com/photo-1611329572665-f3e59fa20438?ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&ixlib=rb-1.2.1&auto=format&fit=crop&w=1500&q=80)`,
      backgroundPosition: "center",
      padding: "35px 25px",
    },
    title: {
      fontSize: 40,
      fontFamily: "Montserrat",
    },
    textContainer: {
      color: "white",
    },
  });
  
  const FeaturedPost = () => {
    const classes = useStyles();
  
    return (
     <>
      <Card className={classes.cover}>
        <CardContent className={classes.textContainer}>
          <Typography className={classes.title} gutterBottom>
            Get Information About Smartphones
          </Typography>
          <Typography variant="h5" component="h2">
          21Mobiles is one of the popular sources for mobile consumers and technology enthusiasts from across the globe.
          </Typography>
        </CardContent>
        <CardActions>
          <Button variant="contained" style={{ color: "black", fontWeight: 600 }}>
            Read More..
          </Button>
        </CardActions>
       
      </Card>
      <Typography align="center"  variant="h5"  gutterBottom={false} component="h5">
  Latest Posts
</Typography>
     
      </>
    );
  };
 
  export default FeaturedPost;