import { Divider, IconButton, makeStyles, Toolbar, Typography } from '@material-ui/core';
import MenuIcon from "@material-ui/icons/Menu"
import React from 'react'
import AccountCircleIcon from '@material-ui/icons/AccountCircle';
import NotificationsIcon from '@material-ui/icons/Notifications';
import { Badge } from '@material-ui/core';
import SidebarDrawer from './SideDrawer';


const useStyle =makeStyles((theme)=>({
title:{
    flexGrow:1,

},
tagline:{
    fontSize:20,
    textTransform:"uppercase",
    justifyContent:"center",
    fontFamily:"Montserrat",
}
}));

const Header = () => {
const  classes =useStyle();

    return (
        <>
        <Toolbar>
        <SidebarDrawer>

        <IconButton color="inherit">
                <MenuIcon/>
            </IconButton>

        </SidebarDrawer>
           
            <Typography variant="h6" className={classes.title}>21Mobiles.com</Typography>
            <IconButton color="inherit">
            <Badge badgeContent={4} color="secondary">
            <NotificationsIcon/>
</Badge>
        
            </IconButton>
            <IconButton color="inherit"><AccountCircleIcon/></IconButton>
        </Toolbar>



<Divider/>
{/* <Toolbar className={classes.tagline}>Informations About Smartphones</Toolbar> */}
       </>
    )
}

export default Header
